const mongoose = require('mongoose');
const bcrypt = require('bcryptjs');

const projectSchema = new mongoose.Schema({
	name: String,
	friendlyUrl: String,
	image: String,
	githubUrl: String,
	livelink: String,
	codelink: String,
	description: String

});

module.exports = mongoose.model('Project', projectSchema);


